export default {
    data() {
        return {
            items: undefined,
            typeText: [
                '',
                '骑行完成',
                '新用户注册',
                '成功邀请用户',
                '上报故障',
                '举报违停',
                '违停一次',
                '违反交通'
            ],
            lastDate: parseInt(Date.now()/1000),
            pullupConfig: {
                content: '上拉加载更多',
                downContent: '松开进行加载',
                upContent: '上拉加载更多',
                loadingContent: '加载中...'
            }
        }
    },
    methods: {
        fetchData() {
            const params = {
                last_date: this.lastDate
            };
            if (this.$route.query.bad_credit_only) {
                params.bad_credit_only = true;
            }
            this.$http({
                url: `${BASE_URL}/lock-account/credit_log`,
                params: params
            }).then(function(result) {
                this.$refs.scroller.donePullup();
                const data = result.data;
                if (data.status == 0) {
                    const length = data.data.length;
                    if (length < 10) {
                        this.$refs.scroller.disablePullup();
                    } else {
                        this.$refs.scroller.enablePullup();
                    }
                    const lastItem = data.data[length - 1];
                    if (lastItem) {
                        this.lastDate = lastItem.create_at;
                    }
                    if (this.items) {
                        this.items = this.items.concat(data.data.slice(1));
                    } else {
                        this.items = data.data;
                    }
                    this.$nextTick(() => {
                        this.$refs.scroller.reset();
                    });
                }
            }.bind(this));
        },
    },
    created() {
        this.fetchData();
    }
}



